import authPage from '@/components/authorization-page.vue' //引入授权窗体
//引入store的内容
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
export default {
	computed: mapState(['token', 'downResult']),
	components: {
		authPage
	},
	props: {
		menuHeight: {
			type: String,
			default: '0'
		},
		typeList: {
			type: Array,
			default: []
		},
		list: {
			type: Array,
			default: []
		}
	},
	data() {
		return {
			selectLeftId: 0, //0代表全部
			chooseItem: {} //用户选中的商品
		}
	},
	methods: {
		//跳转宝贝详情页，传入宝贝id
		turn(id) {
			this.$turnPage('cart/goodsdetail?id=' + id, 'navigateTo');
		},
		setLeftId(id) {
			this.selectLeftId = id;
			this.$emit('queryList', id)
		},
		saveCart(item, isDownLoad) {
			this.chooseItem = item
			if(item.vipPrice == 0) {
				//如果免费，直接改为下载操作
				isDownLoad = true
			}
			let params = {
				item: item,
				isDownLoad: isDownLoad
			}
			this.$emit('saveCart', params)
		}
	}
}
